  }
  .header__logo::before {
    left: -79px;
    height: 91px;
  }
  .header__menu {
    position: static;
    visibility: visible;
    opacity: 1;
    transform: translateX(0%);
  }
  .header__menu-body {
    background-color: transparent;
    padding: 0;
    width: 100%;
  }
  .header__menu-links {
    flex-direction: row;
    border: none;
    padding: 0;
  }
  .header__menu-link {
    white-space: nowrap;
    margin-bottom: 0;
    margin: 0 12px;
    font-size: 16px;
    line-height: 24px;
  }
  .header__menu-link:hover path {
    stroke: #73CBE8;
  }
  .header__lang-link {
    font-size: 16px;
  }
  .header__burger {
    display: none;
  }
  .header__login {
    position: static;
    transform: translateX(0%);
    visibility: visible;
    opacity: 1;
  }
  .header__login-body {
    flex-direction: row;
    background-color: transparent;
    padding: 0;
    width: 100%;
  }
  .header__login-link {
    display: flex;
    align-items: center;
    align-self: center;
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 0;
  }
  .header__register-btn {
    font-size: 16px;
