    margin: 0 15px;
  }
}

@media screen and (min-width: 1530px) {
  .header__logo::before {
    width: 470px;
    left: -294px;
  }
}

@media screen and (min-width: 1920px) {
  .header__menu-link {
    font-size: 18px;
    line-height: 29px;
  }
  .header__logo::before {
    width: 870px;
    left: -591px;
  }
  .header__logo-svg {
    width: 119px;
    height: 20px;
  }
  .header__login-link {
    font-size: 18px;
    line-height: 29px;
  }
  .header__register-btn {
    padding: 10px 35px;
    font-size: 18px;
    line-height: 29px;
  }
  .header__lang-link {
    font-size: 18px;
    line-height: 29px;
  }
}

