.slide {
  height: 200px;
}

.slide__body {
  position: relative;
}

.slide__list {
  position: absolute;
  display: flex;
  align-items: center;
  margin: 0 -22px;
  overflow-x: scroll;
  height: 209px;
  z-index: 2;
  top: -98px;
  width: 100%;
}

.slide__list::-webkit-scrollbar {
  display: none;
}

.slide__list-body {
  padding: 0 22px;
}

.slide__list-card {
  background: url(../img/slide/mansSmall.jpg);
  background-repeat: no-repeat;
  background-position: center;
  height: 197px;
  width: 198px;
  position: relative;
  border-radius: 0 0 30px 10px;
  cursor: pointer;
}

.slide__list-card::before {
  content: '';
  position: absolute;
  width: 212px;
  background-color: transparent;
  bottom: 99px;
  left: -7px;
  height: 101px;
  border-top: 7px solid #FFFFFF;
  border-left: 7px solid #FFFFFF;
  border-right: 7px solid #FFFFFF;
  border-radius: 10px 10px 0 0;
  z-index: 3;
}

.slide__list-card:hover::before {
  background-color: rgba(15, 38, 62, 0.59);
  border-top: 7px solid #73CBE8;
  border-left: 7px solid #73CBE8;
  border-right: 7px solid #73CBE8;
}

.slide__list-card::after {
  content: 'Реферальные программы';
  position: absolute;
  display: flex;
  align-items: center;
  font-size: 16px;
  line-height: 24px;
  color: #FFFFFF;
  padding: 20px 15px;
  width: 212px;
  background-color: rgba(15, 38, 62, 0.59);
  top: 98px;
  left: -7px;
  height: 106px;
  border-bottom: 7px solid #73CBE8;
  border-left: 7px solid #73CBE8;
  border-right: 7px solid #73CBE8;
  border-radius: 0 0 30px 10px;
}

.slide__list-cardBonus {
  background: url(../img/slide/bonus.jpg);
}

.slide__list-cardBonus::after {
  content: 'Бонусы до 30%';
}

.slide__list-cardBuild {
  background: url(../img/slide/build.jpg);
}

.slide__list-cardBuild::after {
  content: 'Гарантия безопасной торговли';
}

.slide__list-cardArms {
  background: url(../img/slide/arms.jpg);
}

.slide__list-cardArms::after {
  content: 'Эксперты международного рынка';
}

.slide__list-text {
  display: flex;
  font-size: 16px;
  line-height: 24px;
  color: #FFFFFF;
  padding: 0 16px;
  padding-top: 133px;
  z-index: 4;
}

@media screen and (min-width: 800px) {
  .slide__list-card {
    width: 230px;
  }
  .slide__list-card::before {
    width: 244px;
  }
  .slide__list-card::after {
    width: 244px;
  }
}

@media screen and (min-width: 1200px) {
