    grid-row: 2/-1;
  }
  .footer__sponsor {
    margin: 0;
    grid-column: 2/-1;
    grid-row: 2/-1;
  }
  .footer__sections {
    display: flex;
    justify-content: space-between;
    display: grid;
    grid-template-columns: 150px 150px 150px;
    grid-template-rows: min-content min-content;
    border-bottom: 2px solid rgba(249, 249, 251, 0.1);
    padding-bottom: 80px;
  }
  .footer__sections-traders {
    border: none;
    padding: 0;
  }
  .footer__sections-analytics {
    border: none;
    padding: 0;
  }
  .footer__sections-education {
    border: none;
    padding: 0;
    grid-column: 2/3;
    margin-top: -185px;
  }
  .footer__sections-company {
    border: none;
    padding: 0;
    grid-column: 1/2;
    grid-row: 2/3;
    margin-top: -97px;
  }
  .footer__sections-documents {
    border: none;
    padding: 0;
    grid-column: 3/-1;
    grid-row: 1/2;
